
















import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  name: 'LoadFile',
  props: {
    accept: {
      type: String,
      default: '*',
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  emits: {
    change: (_e: Event) => undefined,
  },
  setup(_, { emit }) {
    const change = (event: Event) => {
      emit('change', event)
    }

    return {
      change,
    }
  },
  methods: {
    click() {
      if (!this.disabled) (this.$refs.fileReader as HTMLElement).click()
    },
  },
})
